import { Utilities as OfficeUtilities, Storage, StorageType } from '@microsoft/office-js-helpers';
import { MSGraphApiClient } from '../msgraph/msgraph-api-client';

export const cache = new Storage('DocumentPropertiesCache', StorageType.SessionStorage);

/**
  @typedef DocumentProperties
  @type {object}
  @property {string} OFF_CTX_ContentLanguage The locale (language) specified by the user for the document.
  @property {string} OFF_CTX_DisplayLanguage The  locale (language) specified by the user for the UI.
  @property {boolean} OFF_CTX_IsAddin True if the code is running inside of an add-in, otherwise false.
  @property {boolean} OFF_CTX_IsIEOrEdge True if the browser is IE11 or Edge, otherwise false.
  @property {Office.HostType} OFF_CTX_Host The Office application host in which the add-in is running.
  @property {Office.DocumentMode} OFF_CTX_Mode The mode the document is in.
  @property {Office.OfficeTheme} OFF_CTX_OfficeTheme The properties for Office theme colors.
  @property {Office.OfficePlatformType} OFF_CTX_Platform The platform on which the add-in is running.
  @property {boolean} OFF_CTX_TouchEnabled -True if the platform and device allows touch interaction, otherwise false.
  @property {string} OFF_CTX_Url The URL of the document.
  @property {string} OFF_CTX_Version The version of Office on which the add-in is running.
  @property {string} OFF_CTX_FileName The filename of the document.
  @property {string} OFF_CTX_FilePath The path of the document.
  @property {number} SGM_SYS_ActivityID The Activity ID of the Accolade document.
  @property {number} SGM_SYS_DeliverableID The Deliverable ID of the Accolade document.
  @property {number} SGM_SYS_GateDocumentID The Gate Document ID of the Accolade document.
  @property {number} SGM_SYS_ProjectID The Project ID of the Accolade document.
  @property {number} SGM_SYS_RelatedFileID The Related Document ID of the Accolade document.
  @property {string} SGM_SYS_Server The URL of the Accolade server.
  @property {number} SGM_SYS_TemplateLibraryID The Template Library ID of the Accolade document.
 */

/**
 * Loads properties from Excel, PowerPoint and Word files. Optional load properties from related .props file.
 * @async
 * @returns {Promise<DocumentProperties>} The properties.
 */
export async function getPropertiesAsync() {
  let properties = cache.get('properties');
  if (properties && properties['OFF_CTX_Url']) {
    return properties;
  }

  properties = {};
  properties['OFF_CTX_ContentLanguage'] = Office.context.contentLanguage;
  properties['OFF_CTX_DisplayLanguage'] = Office.context.displayLanguage;
  properties['OFF_CTX_IsAddin'] = OfficeUtilities.isAddin;
  properties['OFF_CTX_IsIEOrEdge'] = OfficeUtilities.isIEOrEdge;
  properties['OFF_CTX_Host'] = Office.context.host;
  properties['OFF_CTX_Mode'] = Office.context.document.mode;
  properties['OFF_CTX_OfficeTheme'] = Office.context.officeTheme;
  properties['OFF_CTX_Platform'] = Office.context.platform;
  properties['OFF_CTX_TouchEnabled'] = Office.context.touchEnabled;
  properties['OFF_CTX_Url'] = Office.context.document.url;
  properties['OFF_CTX_Version'] = Office.context.diagnostics.version;

  const url = new URL(properties['OFF_CTX_Url']);
  properties['OFF_CTX_FileName'] = getFileName(url);
  properties['OFF_CTX_FilePath'] = getFilePath(url);

  switch (properties['OFF_CTX_Host']) {
    case Office.HostType.Excel:
      await loadExcelPropertiesAsync(properties);
      break;

    case Office.HostType.Word:
      await loadWordPropertiesAsync(properties);
      break;

    case Office.HostType.PowerPoint:
      await loadPowerPointPropertiesAsync(properties);
      break;
  }

  await loadPropertiesFileAsync(properties);

  cache.set('properties', properties);
  return properties;
}

/**
 * Returns the Accolade server URL.
 * @async
 * @returns {Promise<string>} The Accolade server URL.
 */
export async function getServerUrlAsync() {
  const properties = await getPropertiesAsync();
  return properties['SGM_SYS_Server'];
}

/**
 * Checks if current document is an Accolade smart document.
 * @async
 * @returns {Promise<boolean>} True if document is an Accolade smart document, otherwise false.
 */
export async function isSmartDocumentAsync() {
  const properties = await getPropertiesAsync();
  return properties['SGM_SYS_Server'] && +properties['SGM_SYS_ProjectID'] > 0;
}

function getFileName(url) {
  return url.pathname.split('/').pop();
}

function getFilePath(url) {
  const path = url.pathname.split('/');
  path.splice(1, 1);
  return path.join('/');
}

async function loadExcelPropertiesAsync(properties) {
  try {
    await Excel.run(async context => {
      context.workbook.load('name');
      const customProperties = context.workbook.properties.custom;
      customProperties.load('key,type,value');
      await context.sync();

      for (const property of customProperties.items) {
        properties[property.key] = property.value;
      }
    });
  }
  catch (error) {
    // Ignore.
  }
}

function loadPowerPointPropertiesAsync(/*properties*/) {
  return Promise.resolve();
}

async function loadPropertiesFileAsync(properties) {
  await new Promise(async resolve => {
    let content = '';
    try {
      const graphApi = new MSGraphApiClient();
      content = await graphApi.getFileContentAsync(`${properties['OFF_CTX_FilePath']}.props`);
    }
    catch (error) {
      // Ignore.
    }
    if (content && content.length && content[0] === '{') {
      const props = JSON.parse(content);
      for (const name of Object.keys(props)) {
        const value = props[name];
        if (value !== null) {
          properties[name] = value;
        }
      }
    }

    resolve();
  });
}

async function loadWordPropertiesAsync(properties) {
  try {
    await Word.run(async context => {
      const customProperties = context.document.properties.customProperties;
      customProperties.load('key,type,value');
      await context.sync();
      for (const property of customProperties.items) {
        properties[property.key] = property.value;
      }
    });
  }
  catch (error) {
    // Ignore.
  }
}
