import { Authenticator } from '@microsoft/office-js-helpers';
import { Logger } from '../helpers/logger';
import { ONEDRIVE_CLIENTID } from '../../project.config';

const PROVIDER_NAME = 'OneDrive';
const logger = new Logger(`PROVIDER_NAME ${Authenticator}`);

/**
 * Helper for performing Implicit OAuth Authentication with OneDrive.
 */
export class OneDriveAuthenticator extends Authenticator {
  /**
   * @constructor
   */
  constructor() {
    super();
    this._addEndpoint();
  }

  /**
   * Logon to OneDrive.
   * @async
   * @returns {Promise<OfficeHelpers.IToken>} The access token on success, otherwise null.
   */
  async logonAsync() {
    return await this.authenticate(PROVIDER_NAME).catch(error => {
      logger.error(error);
      return null;
    });
  }

  /**
   * @private
   */
  _addEndpoint() {
    this.endpoints.add(PROVIDER_NAME, {
      authorizeUrl: '/oauth20_authorize.srf',
      baseUrl: 'https://login.live.com',
      clientId: ONEDRIVE_CLIENTID,
      redirectUrl: `${window.location.origin}/onedrive-token-callback.html`,
      responseType: 'token',
      scope: 'files.read',
      state: true
    });
  }
}
