import $ from 'jquery';
import { fabric } from './fabric/office-ui-fabric';
import { getPropertiesAsync } from './helpers/document-properties';
import { htmlEncode } from './helpers/utilities';

Office.initialize = () => {};

Office.onReady(async () => {
  const properties = await getPropertiesAsync();

  const $table = $('.ms-Table tbody');
  for (const key of Object.keys(properties)) {
    const $row = $(`<tr><td>${htmlEncode(key)}</td><td>${htmlEncode(properties[key])}</td></tr>`);
    $table.append($row);
  }

  new fabric.Table($table.closest('.ms-Table')[0]);
});
