import { OneDriveAuthenticator } from './onedrive-authenticator';
import { WebApiClient } from '../helpers/webapi-client';

const SERVER_URL = 'https://graph.microsoft.com';
const API_URL = '/v1.0';

/**
 * MS-Graph API client.
 */
export class MSGraphApiClient {
  /**
   * Gets the content of a OneDrive document.
   * @async
   * @param {string} filePath Path to document.
   * @returns {Promise<any>} The document content.
   */
  async getFileContentAsync(filePath) {
    const token = await new OneDriveAuthenticator().logonAsync();
    if (!token) {
      return;
    }

    const webApi = new WebApiClient(SERVER_URL, { accessToken: token.access_token, accessTokenExpiresIn: token.access_token.expires_in, apiUrl: API_URL });
    return await webApi.getAsync(`~/me/drive/root:${filePath}:/content`);
  }
}
